import { Table } from 'react-bootstrap';
import * as React from 'react';
import * as uuid from 'uuid';
import { CreateProjectDto, projectsService } from './services/api.service';

interface State {
  data: CreateProjectDto[];
}

// tslint:disable-next-line:variable-name
export class ProjectList extends React.Component<{}, State> {

  constructor(props: any) {
    super(props);
    this.state = {
      data: [],
    };
  }

  async componentDidMount() {
    try {
      const response = await projectsService.fetchProjects();
      console.log('Response data from projects service:', response.data);
      this.setState({ data: response.data });
    } catch (error) {
      console.log('Error on c did mount:', error);
    }
  }

  componentDidUpdate() {
    console.log('Component updated, state is: ', this.state);
  }

  render() {
    return (
      <Table striped bordered condensed hover>
        <thead>
        <tr>
          <th>Project ID</th>
          <th>Project Name</th>
          <th>Articles</th>
          <th>Tags</th>
        </tr>
      </thead>
      <tbody>
        {this.state.data.map(d =>
          <tr>
            <td>{uuid.v4()}</td>
            <td>{d.title}</td>
            <td>{d.articles.reduce((accum: string, total: string) => (accum + ', ') + total)}</td>
            <td>{d.tags.reduce((accum: string, total: string) => (accum + ', ') + total)}</td>
          </tr>)}
      </tbody>
    </Table>);
  }
}



